#AboutMe-container{
    height: auto;
    width: 100%;
    border-style: none;
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

#AboutMe-photo{
    border-style: none;
    width: 50%;
    background-color: #679895;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#AboutMe-content{
    width: 50%;
    border-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

#AboutMe-textbox{
    background-color: transparent;
    border-style: none;
    width: 90%;
    margin: 50px auto;
    height: 100%;
    font-size: 24px !important;
    font: 1.34em/1.9em 'Noto Sans', sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    color: #545454;
}

.section-title{
    color: white;
    font-size: 4vw;
}

@media (max-width: 750px){
    #AboutMe-photo{
        display: none;
    }
    #AboutMe-content{
        width: 100%;
        border-style: none;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: center;
            justify-content: center;
        background-color: #679895;
        color: #fff !important;
        -ms-flex-direction: column;
            flex-direction: column;
        height: 100%;
    }
    #AboutMe-textbox{
        height: 60%;
        color: #fff !important;
        text-align: center;
    }
    #AboutMe-textbox p {
        width: 100%;
    }
    #AboutMe-container{
        -ms-flex-direction: column;
            flex-direction: column;
        height: 100vh;
    }
}



